import React, { Fragment, useCallback, useState, useEffect } from "react";
import loadable from "@loadable/component";
// material-ui imports
import { useTheme, useMediaQuery } from "@mui/material";
// styles
import "react-phone-input-2/lib/material.css";
import "swiper/swiper-bundle.min.css";
import useStyles from "./styles/muiStyles";

import { useToastMessage } from "../../../contexts";
import { getPlans } from "../../../api/Plan";

const RH = loadable.lib(() => import("react-helmet"));

const Header = loadable(() => import("../components/Header"));
const Footer = loadable(() => import("../components/Footer"));

const PageHeader = loadable(() => import("./PageHeader"));
const Chatbot = loadable(() => import("./Chatbot"));
const Scheduling = loadable(() => import("./Scheduling"));
const MidBannerOne = loadable(() => import("./MidBannerOne"));
const FAQ = loadable(() => import("./FAQ"));
const MidBannerTwo = loadable(() => import("./MidBannerTwo"));

const Pricing = () => {
  const classes = useStyles();
  const theme = useTheme(); // theme setup
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const toastMessage = useToastMessage();
  const [engagePlans, setEngagePlans] = useState([]);
  const [curatePlans, setCuratePlans] = useState([]);
  const getPlanList = useCallback(async () => {
    const res = await getPlans();
    if (res.error) {
      return toastMessage.setErrorMessage(res.error);
    } else {
      setEngagePlans(res.data.filter((plan) => plan.category === "Engage"));
      setCuratePlans(res.data.filter((plan) => plan.category === "Curate"));
    }
  }, [toastMessage]);

  useEffect(() => {
    getPlanList();
  }, [getPlanList]);

  return (
    <Fragment>
      {/* Head */}
      <RH fallback={null}>
        {({ Helmet }) => (
          <Helmet>
            <title>Pricing - EasySocial.io</title>
            <link rel="canonical" href="https://easysocial.io/" />
            <meta
              name="keywords"
              content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
            />
            <meta
              name="description"
              content="EasySocial offers customisable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
            />
          </Helmet>
        )}
      </RH>
      {/* <Header /> */}
      <Header />
      <PageHeader className={classes["page-header"]} isMobile={isMobile} />
      {/* Chatbot */}
      <Chatbot
        className={classes.chatbot}
        isMobile={isMobile}
        plans={engagePlans}
      />
      {/* Scheduling */}
      <Scheduling
        className={classes.scheduling}
        isMobile={isMobile}
        plans={curatePlans}
      />
      {/* Mid Banner 1 */}
      <MidBannerOne className={classes["mid-banner-one"]} />
      {/* FAQ */}
      <FAQ className={classes.faq} />
      {/* Mid Banner 2 */}
      <MidBannerTwo className={classes["mid-banner-two"]} />
      {/* Footer */}
      <Footer />
    </Fragment>
  );
};

export default Pricing;
